<template>
  <div class="layout-main">
    <div class="cms-page">
      <div class="cms-page-head">
        <div class="cms-page-head-wrap">
          <div class="container-fluid container-cms">
            <h1 class="cms-page-title">Promotions</h1>
          </div>
        </div>
      </div>
      <div class="container-fluid container-cms">
        <div class="cms-page-data">
          <div class="promo-page">
            <div class="radio-group-tabs promo-page-tabs">
              <div class="radio-tab radio-tab-default radio-group-tabs-radio" :class="{ 'radio-tab-active': selectionOption === 'all' }">
                <input type="radio" class="radio-tab-input" id="all" name="promotions-categories" value="all" checked v-model="selectionOption">
                <label for="all" class="radio-tab-block">
                  <span class="radio-tab-label">All</span>
                </label>
              </div>
              <div class="radio-tab radio-tab-default radio-group-tabs-radio" :class="{ 'radio-tab-active': selectionOption === 'limited' }">
                <input type="radio" class="radio-tab-input" id="limited" name="promotions-categories" value="limited" v-model="selectionOption">
                <label for="limited" class="radio-tab-block">
                  <span class="radio-tab-label">Limited promotion</span>
                </label>
              </div>
              <div class="radio-tab radio-tab-default radio-group-tabs-radio" :class="{ 'radio-tab-active': selectionOption === 'regular' }">
                <input type="radio" class="radio-tab-input" id="regular" name="promotions-categories" value="regular" v-model="selectionOption">
                <label for="regular" class="radio-tab-block">
                  <span class="radio-tab-label">Regular promotion</span>
                </label>
              </div>
            </div>
            <div class="promo-page-list" v-if="selectionOption === 'all'">
              <template v-for="item in promoList" :key="item.title">
                <div class="promo-item">
                  <img :src="item.imgUrl" :alt="item.title" class="promo-item-bg">
                  <div class="promo-item-content">
                    <div class="promo-item-description">{{ item.description }}</div>
                    <div class="promo-item-title">{{ item.title }}</div>
                    <div class="promo-item-label">{{ item.label }}</div>
                    <a href="" class="promo-item-btn link-btn link-btn-primary">Show more</a>
                    <a href="" class="promo-item-link link">Terms & Conditions</a>
                  </div>
                </div>
              </template>
            </div>
            <div class="promo-page-list" v-else-if="selectionOption === 'limited'">
              <template v-for="item in limitedPromo" :key="item.title">
                <div class="promo-item">
                  <img :src="item.imgUrl" :alt="item.title" class="promo-item-bg">
                  <div class="promo-item-content">
                    <div class="promo-item-description">{{ item.description }}</div>
                    <div class="promo-item-title">{{ item.title }}</div>
                    <div class="promo-item-label">{{ item.label }}</div>
                    <a href="" class="promo-item-btn link-btn link-btn-primary">Show more</a>
                    <a href="" class="promo-item-link link">Terms & Conditions</a>
                  </div>
                </div>
              </template>
            </div>
            <div class="promo-page-list" v-else-if="selectionOption === 'regular'">
              <template v-for="item in regularPromo" :key="item.title">
                <div class="promo-item">
                  <img :src="item.imgUrl" :alt="item.title" class="promo-item-bg">
                  <div class="promo-item-content">
                    <div class="promo-item-description">{{ item.description }}</div>
                    <div class="promo-item-title">{{ item.title }}</div>
                    <div class="promo-item-label">{{ item.label }}</div>
                    <a href="" class="promo-item-btn link-btn link-btn-primary">Show more</a>
                    <a href="" class="promo-item-link link">Terms & Conditions</a>
                  </div>
                </div>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
@import "../assets/payments.scss";
</style>

<script>
export default {
  data () {
    return {
      promoList: [
        {
          type: 'limited',
          description: 'First deposit bonus',
          title: '100% UP TO 500 USDT!',
          label: '0.03BTC/0.4ETH/5,500doge/6.4LTC',
          imgUrl: 'https://www.eldoah.io/system/comfy/cms/files/files/000/000/198/original/promo-1.jpg',
          moreInfo: ''
        }
      ],
      limitedPromo: [],
      regularPromo: [],
      selectionOption: 'all'
    }
  },
  methods: {
    filters () {
      this.promoList.forEach((item) => {
        if (item.type === 'limited') {
          this.limitedPromo.push(item)
        } else {
          this.regularPromo.push(item)
        }
      })
    }
  },
  created () {
    this.filters()
  }
}
</script>
